function sort(arr) {
   return arr.sort((a, b) => {
      if (a.name.toLocaleLowerCase() < b.name.toLocaleLowerCase()) {
         return -1;
      }
      if (a.name.toLocaleLowerCase() > b.name.toLocaleLowerCase()) {
         return 1;
      }

      return 0;
   })
}

function getContacts(filterBy = null) {
   return new Promise((resolve, reject) => {
      var contactsToReturn = utilService.load(KEY) || contacts;
      if (!utilService.load(KEY) ) utilService.save(KEY, contacts)
      if (filterBy) {
         contactsToReturn = filter(filterBy)
      }
      resolve(sort(contactsToReturn))
   })
}

function getContactById(id) {
   return new Promise((resolve, reject) => {
      const contact = contacts.find(contact => contact._id === id)
      contact ? resolve(contact) : reject(`Contact id ${id} not found!`)
   })
}

function removeContact(id) {
   return new Promise((resolve, reject) => {
      const index = contacts.findIndex(contact => contact._id === id)
      if (index !== -1) {
         contacts.splice(index, 1)
         utilService.save(KEY, contacts)
      }

      resolve(contacts)
   })
}

function _updateContact(contact) {
   return new Promise((resolve, reject) => {
      const index = contacts.findIndex(c => contact._id === c._id)
      if (index !== -1) {
         contacts[index] = contact
         utilService.save(KEY, contacts)
      }
      resolve(contact)
   })
}

function _addContact(contact) {
   return new Promise((resolve, reject) => {
      contact._id = utilService.makeId()
      contact.color = utilService.getRandomColor();
      const contactsToSave = [...contacts]
      contactsToSave.push(contact)
      utilService.save(KEY, contactsToSave)
      
      resolve(contact)
   })
}

function saveContact(contact) {
   return contact._id ? _updateContact(contact) : _addContact(contact)
}

function getEmptyContact() {
   return {
      name: '',
      email: '',
      phone: ''
   }
}

function filter(filterBy) {
   const term = filterBy.term.toLocaleLowerCase();
   var contactsToShow = contacts.filter(contact => {
      return contact.name.toLocaleLowerCase().includes(term) ||
         contact.phone.toLocaleLowerCase().includes(term) ||
         contact.email.toLocaleLowerCase().includes(term)
   })
   return contactsToShow;
}